// component for displaying the blog posts list
import PageTitle from "../../components/utils/PageTitle";
import Post from "./Post";
import { posts } from "../../data/posts";
import { useEffect, useState } from "react";
import Pagination from "../../components/utils/Pagination";
import { pagination } from "../../data/data";

export default function Blog() {
	const [currentPage, setCurrentPage] = useState(0);

	// update document title on page load
	useEffect(() => {
		document.title = "Blog";
	}, []);

	// scroll to top on page load
	useEffect(() => {
		window.scrollTo({ top: 0, behavior: "smooth" });
	}, [currentPage]);

	return (
		<div className="animate__animated animate__fadeIn py-20">
			<PageTitle>Blog</PageTitle>
			<div className="grid grid-cols-1 sm:grid-cols-2 gap-10 mb-20">
				{posts
					.slice(currentPage, currentPage + pagination.itemsPerPage)
					.map((post, index) => (
						<Post key={index} post={post} index={index + currentPage} />
					))}
			</div>
			{/* pagination */}
			{posts.length > pagination.itemsPerPage && (
				<Pagination
					setCurrentPage={setCurrentPage}
					currentPage={currentPage}
					items={posts}
				/>
			)}
		</div>
	);
}
