// component for displaying individual post on the blog page
import { useRef } from "react";
import { Link } from "react-router-dom";
import DefaultImage from "../../components/utils/DefaultImage";
import useObserver from "../../hooks/useObserver";
import * as ROUTES from "../../routes/routes";

export default function Post({ post, index }) {
	const imageRef = useRef(null);
	const isImageVisible = useObserver(imageRef, { threshold: 0 });

	return (
		<div>
			<div className="aspect-w-5 aspect-h-3" ref={imageRef}>
				{/* lazy load image */}
				{isImageVisible ? (
					<img
						src={post.image}
						alt={`post ${index + 1}`}
						className="object-cover"
					/>
				) : (
					<DefaultImage />
				)}
			</div>
			<div className="p-10 bg-gray-100 dark:bg-gray-800">
				<Link to={`${ROUTES.BLOG}/${index + 1}`}>
					<h4 className="text-gray-800 dark:text-white text-lg mb-0">
						{post.title}
					</h4>
				</Link>
				<span className="uppercase text-sm text-gray-400">
					Posted on {post.date}
				</span>
			</div>
		</div>
	);
}
